@tailwind base;
@tailwind components;
@tailwind utilities;


@font-face {
  font-family: Apercu-Regular;
  src: url(/src/assets/fonts/FontsFree-Net-ApercuRegular.ttf);
}

html {
  overflow: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

body {
  margin: 0;
  font-family: Apercu-Regular;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: 1s all;
}